
.dynamic-methods {
    padding: 20px;
    transition: background-color 0.3s ease, color 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .methods-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .btn {
    padding: 10px 15px;
    margin: 5px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .btn-primary {
    background-color: #4779fe;
    color: white;
  }
  
  .results-container {
    justify-content: center;
    width: 100%;
    max-width: 800px;
    padding: 15px;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.1);
    word-wrap: break-word;
    overflow-x: auto;
  }
  
  .results-text {
    white-space: pre-wrap;
    word-break: break-word;
    max-width: 100%;
  }
  
  .clear-container {
    display: flex;
    justify-content: center;
    margin-top: 10px;
  }
  
  .dynamic-methods[data-theme='light'] {
    background-color: #f0f0f0;
    color: #333;
  }
  
  .dynamic-methods[data-theme='dark'] {
    background-color: #333;
    color: #f0f0f0;
  }
  
  /* Hover effects */
  .btn:hover {
    opacity: 0.8;
  }
  
